@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
*{box-sizing:border-box}html{font-family:"Poppins", sans-serif}body{margin:0}.goods-list{min-height:100vh;background-color:#000;color:white;display:flex;flex-direction:column;justify-content:center;align-items:center}.goods-list__start-btn{padding:20px 40px;border:none;box-shadow:#555bff 0 0 0 3px,#1fc11b 0 0 0 6px,#ffd913 0 0 0 9px,#ff9c55 0 0 0 12px,#f55 0 0 0 15px;border-radius:3px;font-size:18px;text-transform:uppercase;color:#fff;cursor:pointer;background-color:#000;transition:background-color 0.5s, color 0.5s, box-shadow 1s}.goods-list__start-btn:hover{background-color:#fff;color:#000;box-shadow:#555bff 0 0 0 3px,#1fc11b 0 0 0 6px,#ffd913 0 0 0 9px,#ff9c55 0 0 0 12px}.goods-list-wrp{padding:60px;box-shadow:blue 0 0 0 2px inset,#fff 10px -10px 0 -3px,#1fc11b 10px -10px,#fff 20px -20px 0 -3px,#ffd913 20px -20px,#fff 30px -30px 0 -3px,#ff9c55 30px -30px,#fff 40px -40px 0 -3px,#f55 40px -40px}.nav__btn{border:1px solid #fff;background-color:#000;padding:10px 20px;margin-left:20px;text-transform:uppercase;cursor:pointer;color:#fff;transition:box-shadow 1s}.nav__btn:first-child{margin-left:0}.nav__btn:hover{border:1px solid #000;box-shadow:#555bff 0 0 0 3px,#1fc11b 0 0 0 6px,#ffd913 0 0 0 9px,#ff9c55 0 0 0 12px}.main{padding:20px}.main__list{list-style:none}

